import { classMap } from 'lit/directives/class-map.js';
import { customElement, property } from 'lit/decorators.js';
import { ifDefined } from 'lit/directives/if-defined.js';
import { css, CSSResultGroup, html, LitElement, TemplateResult } from 'lit';

@customElement('zwc-breadcrumb-item')
export class ZwcBreadcrumbItem extends LitElement {
  @property()
  href?: string;

  @property()
  icon?: string;

  @property()
  target?: '_blank' | '_parent' | '_self' | '_top';

  @property()
  rel = 'noreferrer noopener';

  @property()
  text = '';

  @property({ type: Boolean, reflect: true })
  inverted = false;

  static override styles?: CSSResultGroup | undefined = [
    css`
      :host {
        --zwc-icon-color: var(--c-zurichblue);
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 3px;
      }
      a {
        margin-right: 4px;
        list-style: none;
        padding: 0;
        margin: 0;
        font-size: var(--font-size-root);
        font-family: var(--f-sans-regular);
        color: var(--c-zurichblue);
        text-decoration: none;
        white-space: nowrap;
      }
      a:hover {
        color: var(--c-midblue);
      }
      :host(:last-of-type) a {
        pointer-events: none;
        color: var(--c-darkblue);
      }
      :host(:last-of-type) a:hover {
        color: var(--c-midblue);
      }
      ::slotted(zwc-icon) {
        margin: 0 4px;
      }
      .inverted {
        color: var(--c-white);
      }
      .inverted:hover {
        color: var(--c-lightblue);
      }
      :host(:last-of-type) .inverted {
        color: var(--c-white);
        opacity: 0.66;
      }
      .inverted + zwc-icon,
      .inverted > zwc-icon {
        --zwc-icon-color: var(--c-white);
      }
    `,
  ];

  protected override render(): TemplateResult {
    const classes = {
      inverted: this.inverted,
    };
    return html`
      <a
        class="breadcrumb-item ${classMap(classes)}"
        part="label"
        href="${this.href}"
        target="${ifDefined(this.target ? this.target : undefined)}"
        rel=${ifDefined(this.target ? this.rel : undefined)}
      >
        ${this.icon
          ? html`<zwc-icon size="sm" icon=${this.icon}></zwc-icon>`
          : this.text}
      </a>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-breadcrumb-item': ZwcBreadcrumbItem;
  }
}
